<template>
  <div>
    <section class="contact-banner d-flex">
      <div class="contact-banner__overlay"></div>
      <div class="container align-self-center">
        <div class="row">
          <div class="col-sm-8 offset-sm-2">
            <h1 class="heading text-center">
              Get a Quote
            </h1>
          </div>
        </div>
      </div>
    </section>
    <div class="get-quote-wrapper">
      <section id="cd-google-map">
        <div class="section contact">
          <div class="container">
            <div class="row">
              <div class="col-md-12">
                <h1 class="heading text-center">
                  Request a quote
                </h1>
                <h3 class="text-center mb-30">Simply fill the form below and we will get back to you within 2 business
                  days</h3>
              </div>
            </div>

            <div v-if="errors.length">
              <div class="row">
                <div class="col-12 mb-5">
                  <div class="alert alert-danger">
                    <b>Please correct the following error(s):</b>
                    <ul>
                      <li v-for="error in errors">{{ error }}</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-8 offset-md-2">
                <form @submit.prevent="submitForm" name="contactForm">
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <input v-model="first_name" type="text" class="form-control form-control-lg" id="first-name"
                             required>
                      <label class="form-control-placeholder" for="first-name">Name*</label>
                    </div>
                    <div class="form-group col-md-6">
                      <input type="number" v-model="phone" class="form-control form-control-lg" id="phone" required>
                      <label class="form-control-placeholder" for="phone">Phone*</label>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-12">
                      <input v-model="from" type="text" class="form-control form-control-lg" id="email" required>
                      <label class="form-control-placeholder" for="email">Email*</label>
                    </div>
                  </div>

                  <div class="form-row mt-4">
                    <div class="form-group col-md-12 text-center label-div">
                      <span>What are you looking for?*</span>
                    </div>
                    <br>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="custom_web_application" value="Custom Web Application"
                             v-model="projectType">
                      <label class="ml-2" for="custom_web_application">Custom Web Application</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="drupal_website" value="Drupal Website" v-model="projectType">
                      <label class="ml-2" for="drupal_website">Drupal Website</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="wordpress_website" value="Wordpress Website" v-model="projectType">
                      <label class="ml-2" for="wordpress_website">Wordpress Website</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="ecommerce_solution" value="eCommerce Solution" v-model="projectType">
                      <label class="ml-2" for="ecommerce_solution">eCommerce Solution</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="system_integration" value="System Integration" v-model="projectType">
                      <label class="ml-2" for="system_integration">System Integration</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input type="checkbox"
                             class="styled-checkbox"
                             id="mobile_application" value="Mobile Application" v-model="projectType">
                      <label class="ml-2" for="mobile_application">Mobile Application</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input
                        class="styled-checkbox"
                        type="checkbox" id="spa" value="SPA (Single Page Application)" v-model="projectType">
                      <label class="ml-2" for="spa">SPA (Single Page Application)</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input
                        class="styled-checkbox"
                        type="checkbox" id="hosting_solution" value="Hosting Solution" v-model="projectType">
                      <label class="ml-2" for="hosting_solution">Hosting Solution</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input
                        class="styled-checkbox"
                        type="checkbox" id="security_audit" value="Security Audit" v-model="projectType">
                      <label class="ml-2" for="security_audit">Security Audit</label>
                    </div>
                    <div class="form-group col-12 col-md-6 col-lg-4">
                      <input
                        class="styled-checkbox"
                        type="checkbox" id="others" value="Others" v-model="projectType">
                      <label class="ml-2" for="others">Others</label>
                    </div>
                  </div>

                  <div class="form-row mt-4">
                    <div class="form-group col-md-12 text-center label-div">
                      Project complete duration?*
                    </div>
                    <div class="form-group col-12 col-sm-6 col-md-4 col-lg-3">
                      <input type="radio" class="form-radio" id="asap" value="ASAP" v-model="projectDuration">
                      <label class="ml-2" for="asap">ASAP</label>
                    </div>
                    <div class="form-group col-12 col-sm-6 col-md-4 col-lg-3">
                      <input type="radio" class="form-radio" id="1_3_monts" value="1-3 Months" v-model="projectDuration">
                      <label class="ml-2" for="1_3_monts">1-3 Months</label>
                    </div>
                    <div class="form-group col-12 col-sm-6 col-md-4 col-lg-3">
                      <input type="radio" class="form-radio" id="3_6_months" value="3-6 Months" v-model="projectDuration">
                      <label class="ml-2" for="3_6_months">3-6 Months</label>
                    </div>
                    <div class="form-group col-12 col-sm-6 col-md-4 col-lg-3">
                      <input type="radio" class="form-radio" id="just_budgeting" value="Just Budgeting" v-model="projectDuration">
                      <label class="ml-2" for="just_budgeting">Just Budgeting</label>
                    </div>
                  </div>

                  <div class="form-row mt-4">
                    <div class="form-group col-md-12">
                      <div class="sliderwrap text-center" id="slider-wrap">
                        <label class="labeltext budget-label">Budget: ${{budgetValue[0]}} - ${{budgetValue[1]}}</label>
                        <ejs-slider id="currencySlider" v-model="budgetValue" ref="currencySlider" :value="budgetValue"
                                    :min="min" :max="max" :tooltip="tooltip" :ticks="ticks"
                                    :type="rangetype"></ejs-slider>
                      </div>
                    </div>
                  </div>

                  <div class="form-row mt-4 pt-20">
                    <div class="form-group col-md-12">
                      <textarea v-model="description" id="message" class="form-control" cols="30" rows="10"
                                required></textarea>
                      <label class="form-control-placeholder" for="message">Tell us about your project*</label>
                    </div>
                  </div>

                  <div class="form-row mt-4">
                    <div class="form-group col-md-12">
                      <button class="btn btn-outline-dark" :disabled="ajaxPending" type="submit">
                        Send Message
                      </button>
                      <div class="d-inline-flex align-items-center" v-if="ajaxPending"> &nbsp;
                        &nbsp; <i class="fas fa-circle-notch fa-spin mt-2" style="font-size:24px"></i>
                      </div>
                    </div>
                    <div class="col-12" ref="successAlert">
                      <div class="alert alert-success">
                        Thank you for your enquiry, we'll get back to you shortly.
                      </div>
                    </div>
                    <div class="col-12" ref="errorsAlert">
                      <div class="alert alert-danger">
                        Sorry, some error occurred. Please try again later, or call us at +91 9176333857.
                      </div>
                    </div>
                    <div class="col-12" ref="validationError">
                      <div class="alert alert-danger">
                        Please check the required field and submit again.
                      </div>
                    </div>
                  </div>

                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import {mapMixin} from '../mixins/mapMixin';
import Footer from '../components/Footer';

export default {
  name: 'Quote',
  mixins: [mapMixin],
  mounted () {
    this.initMap();
  },
  components: {
    'app-footer': Footer,
  },
  created() {
    // this.resource = this.$resource('api/v1/create');
    this.resource = this.$resource('php/mailer.php');
  },
  data: function () {
    return {
      errors: [],
      first_name: null,
      phone: null,
      from: null,
      description: null,
      projectType: [],
      projectDuration: null,
      resource: {},
      ajaxPending: false,
      min: 0,
      max: 200000,
      budgetValue: [5000, 40000],
      tooltip: {
        placement: "Before",
        isVisible: true,
        format: "c0",
        color: '#5d5d5d'
      },
      ticks: {
        placement: "After",
        largeStep: 100000,
        smallStep: 5000,
        showSmallTicks: true,
        format: "c0",
        color: '#5d5d5d'
      },
      rangetype: "Range",
    };
  },
  mounted() {
    this.$refs.successAlert.style.display = 'none';
    this.$refs.errorsAlert.style.display = 'none';
    this.$refs.validationError.style.display = 'none';
  },
  methods: {
    resetForm() {
      this.first_name = null;
      this.phone = null;
      this.from = null;
      this.description = null;
      this.projectType = [];
      this.projectDuration = null;
      this.budgetValue = [5000, 40000];
    },
    submitForm() {
      this.errors = [];
      if (!this.validEmail(this.from)) {
        this.errors.push('Valid email required.');
      }
      if (!this.projectType.length) {
        this.errors.push('Please select "What are you looking for?".');
      }

      if (!this.projectDuration) {
        this.errors.push('Please select "Project complete duration".');
      }
      if (this.errors.length) {
        this.$refs.validationError.style.display = 'block';
        setTimeout(() => {
          this.$refs.validationError.style.display = 'none';
        }, 3000);
        return true;
      }

      let payload = {
        first_name: this.first_name,
        phone: this.phone,
        email: this.from,
        project_type: this.projectType,
        project_duration: this.projectDuration,
        budget: '$' + this.budgetValue[0] + ' - $' + this.budgetValue[1],
        description: this.description
      };
      this.ajaxPending = true;
      this.resource.save(payload).then(data => {
        this.ajaxPending = false;
        this.$refs.successAlert.style.display = 'block';
        setTimeout(() => {
          this.$refs.successAlert.style.display = 'none';
        }, 3000);
        this.resetForm();
      }, (err) => {
        if (err) {
          console.log(err);
        }
        this.$refs.errorsAlert.style.display = 'block';
        setTimeout(() => {
          this.$refs.errorsAlert.style.display = 'none';
        }, 3000);
        this.ajaxPending = false;
        this.resetForm();
      });
    },
    validEmail: function (email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  }
};
</script>

<style scoped lang="scss">
  @import "../../node_modules/@syncfusion/ej2-base/styles/material.css";
  @import "../../node_modules/@syncfusion/ej2-buttons/styles/material.css";
  @import "../../node_modules/@syncfusion/ej2-popups/styles/material.css";
  @import "../../node_modules/@syncfusion/ej2-vue-inputs/styles/material.css";
  @import "../assets/css/get-quote.css";

  .label-div{
    padding-bottom:1.5rem;
  }
  .budget-label{
    font-size:1.5rem;
    padding-bottom:1.5rem;
    padding-top:1.5rem;
  }
  .styled-checkbox {
    position: absolute; // take it out of document flow
    opacity: 0; // hide it

    & + label {
      position: relative;
      cursor: pointer;
      padding: 0;
    }

    // Box.
    & + label:before {
      content: '';
      margin-right: 10px;
      display: inline-block;
      vertical-align: text-top;
      width: 20px;
      height: 20px;
      background: white;
    }

    // Box hover
    &:hover + label:before {
      background: #f7f7f7;;
    }

    // Box focus
    &:focus + label:before {
      box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
    }

    // Box checked
    &:checked + label:before {
      background: #585a5c;
    }

    // Disabled state label.
    &:disabled + label {
      color: #b8b8b8;
      cursor: auto;
    }

    // Disabled box.
    &:disabled + label:before {
      box-shadow: none;
      background: #ddd;
    }

    // Checkmark. Could be replaced with an image
    &:checked + label:after {
      content: '';
      position: absolute;
      left: 5px;
      top: 10px;
      background: white;
      width: 2px;
      height: 2px;
      box-shadow:
        2px 0 0 white,
        4px 0 0 white,
        4px -2px 0 white,
        4px -4px 0 white,
        4px -6px 0 white,
        4px -8px 0 white;
      transform: rotate(45deg);
    }
  }
  .form-radio {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: inline-block;
    position: relative;
    background-color: #fff;
    color: #666;
    top: 10px;
    height: 25px;
    width: 25px;
    border: 0;
    border-radius: 50px;
    cursor: pointer;
    margin-right: 7px;
    outline: none;
  }
  .form-radio:checked::before {
    position: absolute;
    font: 13px/1 'Open Sans', sans-serif;
    left: 9px;
    top: 5px;
    content: '\02143';
    transform: rotate(40deg);
  }
  .form-radio:hover {
    background-color: #f7f7f7;
  }
  .form-radio:checked {
    background-color: #f1f1f1;
  }
  label {
   /* font: 15px/1.7 'Open Sans', sans-serif; */
    font-size:13px;
    font-family: 'Open Sans', sans-serif;
    color: #333;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
  }

  @media screen and (max-width: 480px) {
    #cd-google-map-content .heading {
      font-size: 4rem;
    }
  }


</style>
